
html,
body,
button,
caption,
dd,
div,
dl,
dt,
fieldset,
figure,
form,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
html,
input,
legend,
li,
menu,
ol,
p,
table,
td,
textarea,
th,
ul {
  -webkit-user-select: text;
     -moz-user-select: text;
      -ms-user-select: text;
          user-select: text;
  margin: 0;
  padding: 0;
}
address,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
caption,
th {
  text-align: left;
  font-weight: normal;
}
abbr,
body,
fieldset,
html,
iframe,
img {
  border: 0;
}
address,
cite,
dfn,
em,
i,
var {
  font-style: normal;
}
[hidefocus],
summary {
  outline: 0;
}
li {
  list-style: none;
}
code,
kbd,
pre,
samp {
  font-family: inherit;
}
q:after,
q:before {
  content: none;
}
textarea {
  overflow: auto;
  resize: none;
}
label,
summary {
  cursor: default;
}
a,
button {
  cursor: pointer;
}
b,
em,
h1,
h2,
h3,
h4,
h5,
h6,
strong {
  font-weight: bold;
}
a,
a:hover,
del,
ins,
s,
u {
  text-decoration: none;
}
body,
button,
input,
keygen,
legend,
select,
textarea {
  font: 12px/1 Helvetica,Microsoft YaHei,\5FAE\8F6F\96C5\9ED1;
  color: #666;
  outline: 0;
}
body {
  background: #fff;
}
a,
a:hover {
  color: #459ae9;
}
.fix:before, .fix:after {
  content: "";
  display: table;
}
.fix::after {
  clear: both;
}
.g-hd {
  width: 100%;
  height: 80px;
  background-color: #2dcc70;
}
.g-box {
  width: 1200px;
  padding: 0 30px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
}
.u-logo {
  float: left;
  line-height: 80px;
  font-size: 30px;
  color: #fff;
  font-weight: bold;
}
.u-desc {
  float: right;
  line-height: 80px;
  font-size: 18px;
  color: #fff;
}
.g-bd {
  padding-top: 55px;
  padding-bottom: 95px;
}
.g-menu {
  float: left;
  width: 240px;
  margin-right: 30px;
}
.g-menu > p {
    width: 100%;
    font-size: 16px;
    color: #5e6d82;
    line-height: 40px;
    padding-bottom: 6px;
    border-bottom: 1px solid #eaeefb;
}
.g-menu .menu li {
    display: block;
    height: 40px;
    line-height: 40px;
    font-size: 13px;
    padding-left: 24px;
}
.g-menu .menu li > a {
      display: block;
      height: 100%;
      color: #5e6d82;
}
.g-menu .menu li > a.active {
        color: #459ae9;
}
.g-ct {
  float: right;
  width: 870px;
}
.g-ct h2 {
    font-size: 28px;
    font-weight: 400;
    color: #1f2f3d;
}
.g-ct h3 {
    font-size: 22px;
    margin: 45px 0 15px;
    font-weight: 400;
    color: #1f2f3d;
}
.g-ct p {
    margin: 1em 0;
    font-size: 14px;
    color: #5e6d82;
    line-height: 1.5em;
}
.g-ct pre {
    font-size: 0;
    padding: 0;
    height: auto;
}
.g-ct code {
    line-height: 1.8;
    font-family: Menlo,Monaco,Consolas,Courier,monospace;
    font-size: 12px;
    background-color: #f9fafc;
    border: 1px solid #eaeefb;
    border-radius: 4px;
    padding: 0;
}
.g-ct table {
    border-collapse: collapse;
    width: 100%;
    background-color: #fff;
    color: #5e6d82;
    font-size: 14px;
    margin-bottom: 45px;
    line-height: 1.5em;
}
.g-ct table th {
      text-align: left;
      border-top: 1px solid #eaeefb;
      background-color: #eff2f7;
      white-space: nowrap;
}
.g-ct table th,
    .g-ct table td {
      border-bottom: 1px solid #eaeefb;
      padding: 10px;
      max-width: 250px;
}



