
.jo-tree {
  cursor: default;
  background: #fff;
  border: none;
}
.jo-tree__empty-block {
  position: relative;
  min-height: 60px;
  text-align: center;
  width: 100%;
  height: 100%;
}
.jo-tree__empty-text {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: #5e7382;
}
.jo-tree-node {
  white-space: nowrap;
}
.jo-tree-node > .jo-tree-node__children {
  overflow: hidden;
  background-color: transparent;
}
.jo-tree-node.is-expanded > .jo-tree-node__children {
  display: block;
}
.jo-tree-node__expand-icon,
.jo-tree-node__label,
.jo-tree-node__loading-icon {
  display: inline-block;
  vertical-align: middle;
}
.jo-tree-node__content {
  line-height: 36px;
  height: 36px;
  cursor: pointer;
}
.jo-tree-node__content > .jo-checkbox,
.jo-tree-node__content > .jo-tree-node__expand-icon {
  margin-right: 8px;
}
.jo-tree-node__content > .jo-checkbox {
  vertical-align: middle;
}
.jo-tree-node__content:hover {
  background: #e4e8f1;
}
.is-leaf-ct:hover {
  transition: all 0.3s;
  color: #fff;
  background-color: #2dcc70;
}
.jo-tree-node__expand-icon {
  cursor: pointer;
  width: 0;
  height: 0;
  margin-left: 10px;
  border: 6px solid transparent;
  border-right-width: 0;
  border-left-color: #97a8be;
  border-left-width: 7px;
  transform: rotate(0);
  transition: transform .3s ease-in-out;
}
.jo-tree-node__expand-icon:hover {
  border-left-color: #999;
}
.jo-tree-node__expand-icon.expanded {
  transform: rotate(90deg);
}
.jo-tree-node__expand-icon.is-leaf {
  border-color: transparent;
  cursor: default;
}
.jo-tree-node__label {
  font-size: 14px;
}
.jo-tree-node__loading-icon {
  margin-right: 4px;
  font-size: 14px;
  color: #97a8be;
}
.jo-tree--highlight-current .jo-tree-node.is-current > .jo-tree-node__content {
  background-color: #edf7ff;
}
.jo-tree--highlight-current .jo-tree-node.is-current > .is-leaf-ct {
  transition: all 0.3s;
  color: #fff;
  background-color: #2dcc70;
}



