
.jo-breadcrumb {
  display: inline-block;
  line-height: 1;
  font-size: 0;
}
.jo-breadcrumb-item {
    display: inline-block;
    vertical-align: middle;
    color: #999;
}
.jo-breadcrumb-item span {
      display: inline-block;
      vertical-align: middle;
      font-size: 14px;
}
.jo-breadcrumb-item:last-child .jo-breadcrumb-item-separator {
      display: none;
}
.jo-breadcrumb-item-separator {
      margin-left: 6px;
      margin-right: 6px;
}
.jo-breadcrumb-item-link {
      color: #459ae9;
      cursor: pointer;
}



