
.author-wrap {
  text-align: center;
}
.author-wrap .author-head {
    position: relative;
}
.author-wrap .author-head #author {
      border-radius: 10px;
}
.author-wrap .author-head #triangle {
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
}
.author-wrap .author-head .author-img {
      position: absolute;
      top: 48%;
      right: 50px;
      transform: translate3d(0, -50%, 0);
      width: 120px;
      height: 120px;
      border-radius: 50%;
}
.author-wrap .author-body {
    font-size: 15px;
}
.author-wrap .author-body p {
      margin: 0;
}



