
.jo-selectbox[data-v-6eca525c] {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  padding: 0 10px;
  min-width: 80px;
  height: 30px;
  font-size: 12px;
  color: #666;
  border: 1px solid #e7e7e7;
  text-align: center;
  line-height: 30px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: #fff;
  transition: all .15s ease-out;
}
.jo-selectbox.checked[data-v-6eca525c] {
    color: #2dcc70;
    border: 1px solid #2dcc70;
}
.jo-selectbox-lg[data-v-6eca525c] {
    min-width: 160px;
    height: 50px;
    line-height: 50px;
    font-size: 14px;
    padding: 0 50px;
}
.jo-selectbox-stress[data-v-6eca525c]:before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 0;
    border: 9px solid #2dcc70;
    border-top-color: transparent;
    border-left-color: transparent;
    opacity: 0;
    transition: all .15s ease-out;
}
.jo-selectbox-stress[data-v-6eca525c]:after {
    content: "\2713";
    position: absolute;
    right: 1px;
    bottom: -3px;
    font-weight: bold;
    color: #fff;
    line-height: 18px;
    opacity: 0;
    transition: all .15s ease-out;
}
.jo-selectbox-stress.checked[data-v-6eca525c]:before {
    opacity: 1;
}
.jo-selectbox-stress.checked[data-v-6eca525c]:after {
    opacity: 1;
}



